select {
    -webkit-appearance:none;
    appearance:none;
}
.dummy_disabled,
.dummy_active {
    overflow: hidden;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -12.5px 0 0 -12.5px;
    width: 25px;
    height: 25px;
    background-color: #000;
    border-radius: 50%;
    opacity: 0;
}

.dummy_active {
    background-color: #ff6000;
}

.ani {
    -webkit-animation-name: animation;
    animation-name: animation;
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
}

@-webkit-keyframes animation {
    0% {
        opacity: 0.3;
        transform: scale(1, 1);
        -webkit-transform: scale(1, 1);
    }
    100% {
        opacity: 0;
        transform: scale(2, 2);
        -webkit-transform: scale(2, 2);
    }
}

@keyframes animation {
    0% {
        opacity: 0.3;
        transform: scale(1, 1);
        -webkit-transform: scale(1, 1);
    }
    100% {
        opacity: 0;
        transform: scale(2, 2);
        -webkit-transform: scale(2, 2);
    }
}