.wrap {
    position: relative;
    width: 414px;
    margin: 0 auto;
}

.intro {
    height: 846px;
    background: url(../img/intro.png) no-repeat;
    background-size: 414px 846px;
}

.gnb {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    .btn_first {
        display: inline-block;
        position: relative;
        width: 53px;
        height: 57px;
    }
    .btn_middle {
        position: absolute;
        top: 0;
        left: 50%;
        width: 125px;
        height: 57px;
        margin-left: -62px;
    }
    .btn_last {
        position: absolute;
        top: 0;
        right: 0;
        width: 57px;
        height: 57px;
    }
}

.main {
    height: 846px;
    background: url(../img/main.png) no-repeat;
    background-size: 100% 100%;

    .tab {
        position: absolute;
        top: 57px;
        left: 15px;
        right: 15px;
        height: 55px;

        .btn {
            position: relative;
            float: left;
            width: 33.33%;
            height: 100%;
        }
    }

    .main_menu {
        overflow: hidden;
        position: absolute;
        top: 132px;
        left: 14px;
        right: 14px;
        height: 220px;

        .btn {
            position: relative;
            float: left;
            width: 79px;
            height: 60px;
            margin: 0 0 69px 23px;

            &:nth-child(4n + 1) {
                margin-left: 0;
            }
            .text {
                position: absolute;
                top: 69px;
                left: 27px;
                width: 26px;
                height: 10px;
                color: transparent;
                font-size: 0;

                -webkit-animation-name: animation_text;
                animation-name: animation_text;
                -webkit-animation-duration: 1s;
                animation-duration: 1s;
                animation-iteration-count: infinite;
            }
        }
        .dummy_active {
            -webkit-animation-name: animation;
            animation-name: animation;
            -webkit-animation-duration: 1s;
            animation-duration: 1s;
            animation-iteration-count: infinite;
        }
    }

    .btn_banner1 {
        position: absolute;
        top: 412px;
        left: 18px;
        width: 286px;
        height: 35px;
    }

    .btn_banner2 {
        position: absolute;
        top: 470px;
        left: 17px;
        width: 380px;
        height: 108px;
    }
    .btn_banner3 {
        position: absolute;
        top: 595px;
        left: 17px;
        width: 380px;
        height: 234px;
    }
}

@-webkit-keyframes animation_text {
    0% {
        opacity: 1;
        background: #fff url(../img/loan_text_active.png) no-repeat;
        background-size: 25px 10px;
    }
    100% {
    }
}

.loan {
    position: relative;
    min-height: 846px;
    padding: 57px 17px 0;
    box-sizing: border-box;

    &.step1 {
        height: 1033px;
        background: url(../img/step1.png) no-repeat;
        background-size: 414px 1033px;

        .btn_management {
            position: absolute;
            top: 226px;
            left: 17px;
            width: 323px;
            height: 30px;
        }

        .btn_guidelines {
            position: absolute;
            bottom: 40px;
            left: 91px;
            width: 232px;
            right: 80px;
            height: 29px;
        }

        .product {
            padding: 250px 0 0;

            a {
                position: relative;
                float: left;
                width: 184px;
                height: 188px;
                margin: 0px 0 11px 12px;

                &:nth-child(2n + 1) {
                    margin-left: 0;
                }
            }
        }
    }
    &.nav {
        background: url(../img/gnb.png) no-repeat;
        background-size: 414px 57px;
    }
    .step_status {
        position: relative;
        height: 54px;
        margin: 0 -17px;
        padding: 0 17px;
        border-bottom: 1px solid #cacaca;

        .tit {
            display: table-cell;
            height: 33px;
            padding: 10px 0;
            line-height: 15px;
            vertical-align: middle;
            font-size: 12px;
            font-weight: bold;
        }
        .num_area {
            position: absolute;
            top: 10px;
            right: 17px;

            .num {
                overflow: hidden;
                float: left;
                width: 33px;
                height: 33px;
                margin-left: 7px;
                border-radius: 50%;
                color: #888;
                text-align: center;
                line-height: 33px;
                font-size: 14px;

                &:first-child {
                    margin-left: 0;
                }
                &.current {
                    background: #ef8535;
                    color: #fff;
                }
                &.checked {
                    position: relative;
                    font-size: 0;

                    &:after {
                        content: '';
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        width: 12px;
                        height: 8px;
                        margin: -4px 0 0 -6px;
                        background: url(../img/ico_checked.png) no-repeat;
                        background-size: 100%;
                    }
                }
            }
        }
    }
    .form {
        padding-top: 5px;
        margin-bottom: 28px;
        border-top: 1px solid #cacaca;

        &.first {
            border-top: 0;
        }

        .section_title {
            margin: 20px 0 -8px;
            color: #212121;
            font-size: 18px;
            font-weight: bold;
        }
        .cell {
            overflow: hidden;
            margin-top: 23px;

            &.only_text{
                .tit {
                    &:after {
                        height: 20px;
                    }
                }
                .inp_area {
                    line-height: 20px;
                }
            }
            .tit {
                float: left;
                width: 126px;

                .inner {
                    display: inline-block;
                    line-height: 17.5px;
                    vertical-align: middle;
                    font-size: 15px;
                }
                &:after {
                    content: '';
                    display: inline-block;
                    height: 52px;
                    vertical-align: middle;
                }
            }
            .inp_area {
                overflow: hidden;
                line-height: 52px;

                .inner {
                    float: left;
                    width: 69px;

                    &.inner_sel {
                        position: relative;
                        width: 68px;
                    }
                }
                .dash {
                    overflow: hidden;
                    float: left;
                    width: 23px;
                    height: 52px;
                    background: url(../img/dash.png) no-repeat 50% 50%;
                    color: transparent;
                    font-size: 1px;
                    text-indent: -100px;
                }
            }
            .inp_text {
                width: 100%;
                height: 52px;
                padding: 0 12px;
                border: 1px solid #69b6df;
                border-radius: 8px;
                background: #ddf3fe;
                box-sizing: border-box;
                color: #212121;
                font-size: 15px;

                &.center {
                    text-align: center;
                }
                &.right {
                    text-align: right;
                }
                &[readonly] {
                    border-color: #e3e3e3;
                    background-color: #f7f7f7;
                    color: #888;
                }
            }
            .inp_select {
                width: 100%;
                height: 52px;
                padding-left: 10px;
                border: 1px solid #69b6df;
                border-radius: 8px;
                box-sizing: border-box;
                color: #212121;
                font-size: 15px;
                background: #ddf3fe url(../img/ico_arrow.png) no-repeat 45px 22px;
                background-size: 12px 7px;

                &[readonly] {
                    padding-left: 20px;
                    border-color: #e3e3e3;
                    background-color: #f7f7f7;
                    background-image: none;
                    color: #888;
                }
            }
        }
    }
    .agree_section {
        margin-top: 22px;

        .item {
            margin-top: 39px;

            &:first-child {
                margin-top: 0;
            }
            .box {
                position: relative;
                padding: 15px;
                border: 1px solid #dfdfdf;
                border-radius: 10px;
                background: #f7f7f7;

                em {
                    display: block;
                    color: #888;
                    font-size: 13px;
                }
                p {
                    margin-top: 14px;
                    color: #4f4f4f;
                    line-height: 20px;
                    font-size: 15px;
                }
                .btn_more {
                    position: absolute;
                    top: 15px;
                    right: 15px;
                    color: #ef8535;
                    font-size: 13px;
                    text-decoration: underline;
                }
            }
            .desc {
                margin-top: 20px;
                color: #4f4f4f;
                text-align: center;
                font-size: 15px;
            }
        }
        .check_area {
            margin-top: 15px;
            text-align: center;

            .inner {
                display: inline-block;
                margin-left: 40px;

                &:first-child {
                    margin-left: 0;
                }
                input:checked + label:before {
                    background: url(../img/ico_radio_on.png) no-repeat;
                    background-size: 100% 100%;
                }
                label {
                    display: inline-block;
                    position: relative;
                    padding-left: 34px;
                    color: #4f4f4f;
                    font-size: 15px;
                    line-height: 27px;

                    &:before {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 26px;
                        height: 26px;
                        background: url(../img/ico_radio.png) no-repeat;
                        background-size: 100% 100%;
                    }
                }
            }
        }
        .agree_noti {
            position: relative;
            margin-top: 35px;
            padding-left: 8px;
            color: #ed6c21;
            line-height: 17px;
            font-size: 13px;

            &:before {
                content: '';
                position: absolute;
                top: 6px;
                left: 0;
                width: 3px;
                height: 3px;
                border-radius: 50%;
                background: #000;
            }
        }
    }
    .confirm_section {
        border-top: 1px solid #cacaca;
        padding: 35px 0 17px;

        .tit {
            color: #212121;
            text-align: center;
            line-height: 29px;
            font-size: 23px;
            font-weight: bold;
        }
        .desc {
            margin-top: 10px;
            color: #4f4f4f;
            text-align: center;
            font-size: 15px;
        }

        .banner {
            position: relative;
            margin-top: 27px;
            padding: 15px;
            border: 1px solid #dcdcdc;
            border-radius: 10px;
            background: #f7f7f7;

            em {
                color: #212121;
                font-size: 15px;
                font-weight: bold;
            }
            p {
                margin-top: 15px;
                color: #212121;
                line-height: 20px;
                font-size: 15px;
            }
            a {
                position: absolute;
                top: 15px;
                right: 15px;
                color: #ef8535;
                text-decoration: underline;
                font-size: 13px;
            }
        }
    }

    .table_type {
        width: 100%;
        margin-top: 15px;

        thead {
            th {
                height: 52px;
                border-left: 1px solid #d6d6d6;
                background: #cbcbcb;
                color: #fff;
                font-size: 13px;

                &:first-child {
                    border-left: 0;
                }
            }
        }
        tbody {
            td {
                padding: 17px 0;
                border-left: 1px solid #f9f9f9;
                border-bottom: 1px solid #fff;
                background: #f7f7f7;
                color: #212121;
                line-height: 17px;
                text-align: center;
                font-size: 13px;

                &:first-child {
                    border-left: 0;
                }
            }
        }
    }

    .complete_msg {
        padding: 66px 0 46px;
        color: #212121;
        line-height: 34px;
        text-align: center;
        font-size: 29px;
    }

    .btn_area {
        position: absolute;
        bottom: 17px;
        left: 17px;
        right: 17px;
        text-align: center;
        white-space: nowrap;
        font-size: 0;

        &.default {
            overflow: hidden;
            position: static;
            margin: 20px 0 15px;
        }
        .inner {
            display: inline-block;
            position: relative;
            width: 50%;
            padding-left: 5px;
            box-sizing: border-box;

            &:first-child {
                padding: 0 5px 0 0;
            }
            &:only-child {
                padding-right: 0;
            }
        }
        .btn_cancel {
            width: 100%;
            height: 52px;
            border: 1px solid #c8c8c8;
            border-radius: 10px;
            color: #212121;
            font-size: 15px;
        }
        .btn_confirm {
            width: 100%;
            height: 52px;
            border-radius: 10px;
            background: #ef8535;
            color: #fff;
            font-size: 15px;
        }
    }
}