@charset "utf-8";
/* NHN Technology Services UIT Development Center PSJ */

/* Common */
body, p,
h1, h2, h3, h4, h5, h6,
ul, ol, li,
dl, dt, dd,
table, th, td,
form, fieldset, legend, input, textarea, button, select {
	margin: 0;
	padding: 0;
}

body,
input,
textarea,
select,
button,
table {
	color: #303030;
	font-size: 13px;
	font-family: NotoSans, NanumSquare, dotum, sans-serif;
}

input,
textarea,
select {
    outline: none;
}

html,
body {
	height: 100%;
}

body {
	-webkit-text-size-adjust: none;
}

img,
fieldset {
	border: 0;
}

img {
	vertical-align: top;
}

ul,
ol {
	list-style: none;
}

em,
address {
	font-style: normal;
}

a {
	text-decoration: none;
	color: #303030;
    -webkit-tap-highlight-color:transparent;
}

table {
	border-collapse: collapse;
}

button {
	padding: 0;
	border: 0;
	background: none;
	cursor: pointer;
	outline: none;
    -webkit-tap-highlight-color:transparent;
}
button[disabled] {
	cursor: default;
}

.blind {
	position: absolute;
	clip: rect(0 0 0 0);
	width: 1px;
	height: 1px;
	margin: -1px;
	overflow: hidden;
}
