// dimmed
@mixin dimmed($position, $zIndex, $opacity) {
	position: $position;
	z-index: $zIndex;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background: rgba(0, 0, 0, $opacity);
}

// 센터 정렬
@mixin centerer($horizontal: true, $vertical: true) {
	position: absolute;
	@if ($horizontal and $vertical) {
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		transform: translate(-50%, -50%);
	} @else if ($horizontal) {
		left: 50%;
		-webkit-transform: translate(-50%, 0);
		transform: translate(-50%, 0);
	} @else if ($vertical) {
		top: 50%;
		-webkit-transform: translate(0, -50%);
		transform: translate(0, -50%);
	}
}

//placeholder 색상
@mixin placeholder-color($color) {
	&::-webkit-input-placeholder {
		color: $color;
	}
	&::-moz-placeholder {
		color: $color;
	}
	&:-ms-input-placeholder {
		color: $color;
	}
	&:-moz-placeholder {
		color: $color;
	}
}


@mixin sprite-after($sprite, $sprite_group: $sp_phrasebook) {
    &:after {
        @include get-sprite($sprite, $sprite_group);
        display: block;
        content: '';
    }
}

// 스프라이트 버튼
@mixin button-with-sprite($sprite, $sprite_group: $sp_phrasebook, $padding: $G-padding-button) {
    padding: $padding;
    font-size: 0;
    line-height: 0;
    color: transparent;

    @include sprite-after($sprite, $sprite_group);
}

//
@mixin button-absolute($sprite, $sprite_group: $sp_phrasebook, $padding: $G-padding-button, $top: false, $right: false, $bottom: false, $left: false) {
	position: absolute;
	@if ($top) {
		top: $top - $padding;
	}
	@if ($right) {
		right: $right - $padding;
	}
	@if ($bottom) {
		bottom: $bottom - $padding;
	}
	@if ($left) {
		left: $left - $padding;
	}
    @include button-with-sprite($sprite, $sprite_group, $padding);
}
